.overflow-contact {
    overflow: auto;
    height: 100vh;
}

.contact-header {
    display: flex;
}

.form {
    position: relative;
    margin-left: var(--size-epic);
    padding: var(--size-large);
}

@media (max-width: 720px) {
    .form {
        margin-left: var(--size-small);
        padding: var(--size-small);
    }

    fieldset {
        padding: var(--size-small);
    }
}

.input-wrapper {
    display: flex;
    flex-flow: column;
    align-items: start;
    font-size: var(--size-large);
}

.send {
    display: flex;
}

input {
    font: inherit;
    padding: var(--size-small);
    width: 90%;
    border-radius: var(--size-tiny);
    font-size: var(--size-large);
    border-width: 1px;
}

fieldset {
    margin-bottom: var(--size-bigger);
    width: 60%;
    padding: var(--size-huge);
    background-color: #e6e6ee;
    border-radius: var(--size-small);
    box-shadow: 0 var(--size-micro) var(--size-tiny) rgba(0,0,0,.16);
    border-color: lightgrey;
}

textarea {
    resize: none;
    font: inherit;
    padding: var(--size-small);
    width: 90%;
    height: 200px;
}