:root{
  --bg-color: #D8D8D8;

  --size-micro: 0.125rem;
  --size-tiny: 0.25rem;
  --size-small: 0.5rem;
  --size-medium: 0.75rem;
  --size-large: 1rem;
  --size-larger: 1.75rem;
  --size-big: 2rem;
  --size-bigger: 2.5rem;
  --size-huge: 4rem;
  --size-epic: 8rem;

  --small-bp: 720px;
  --medium-bp: 1080px;
}

.App {
  text-align: center;
}

.App-header {
  background-image: radial-gradient(ellipse at center,#f8f8f8 0,#bbbbd1 100%);
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(82, 78, 78);
  overflow: hidden;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.button {  
  border: 1px solid #070808;   
  color: #000000;
  padding: var(--size-small) var(--size-small);
  cursor: pointer;
  border-radius: var(--size-tiny);
  justify-content: center;
  min-width: 90px;
  font: inherit;
  text-decoration: unset;
  font-size: var(--size-medium);
}
.button:hover {
  background-color: rgb(179, 187, 201);
}

.primary-button {
  background-color: #fff;
  box-shadow: 0 var(--size-micro) var(--size-tiny) rgba(0,0,0,.16);
}

.secondary-button {
  background-color: transparent;
}
