body {
  margin: 0;
  font-family: FireSans-Regular;
}

@font-face {
  font-family: 'FireSans-Regular';
  src: url('./static/fonts/FiraSans-Regular.ttf') format('truetype'),
       url('./static/fonts/FiraSans-ExtraBold.ttf') format('truetype'),
       url('./static/fonts/FiraSans-Light.ttf') format('truetype'),
       url('./static/fonts/FiraSans-Medium.ttf') format('truetype'),
       url('./static/fonts/FiraSans-Regular.ttf') format('truetype'),
       url('./static/fonts/FiraSans-SemiBold.ttf') format('truetype');
}
