.nav-container {
    margin-top: var(--size-large);
    margin-right: var(--size-medium);
    margin-left: var(--size-medium);
    z-index: 1;
    float: right;
    position: relative;
}

.nav-container > :first-child {
    margin-top: 0;
}

.nav-container > * {
    margin-left: var(--size-small);
}

@media (max-width: 1080px)  {
    .nav-container > * {
        margin-top: var(--size-small);
        margin-left: 0;
    }
    .nav-container {
        margin-right: var(--size-medium);
        display: flex;
        flex-flow: column;
        align-items: center;
    }
}
